import React, { Component } from "react";
import axios from "axios";

export default class Register extends Component {
  state = {
    Username: "",
    Password: "",
    Email: "",
    showLogin: false,
    showError: false,
    showPasswordError: false,
    showEmailError: false,
  };
  handlerChange = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]: e.target.value });
    this.setState({ showError: false });
    this.setState({ showPasswordError: false });
    this.setState({ showEmailError: false });
    console.log();
  };

  changeButton = () => {
    this.setState({ showLogin: !this.state.showLogin });
  };

  emailIsValid = (email) => {
    return /\S+@\S+\.\S+/.test(email);
  };

  handleSubmit = () => {
    var Username = document.querySelector("#Username").value;
    var Password = document.querySelector("#Password").value;
    var Email = document.querySelector("#Email").value;
    var Confirm = document.querySelector("#Confirm").value;

    if (Username === "" || Password === "" || Email === "") {
      this.setState({ showError: true });
      this.setState({ showPasswordError: true });
      this.setState({ showEmailError: true });
    } else if (this.emailIsValid(Email) === false) {
      this.setState({ showEmailError: true });
    } else if (Password !== Confirm) {
      this.setState({ showPasswordError: true });
    } else {
      console.log(this.state);
      axios
        .post("http://localhost:3001/users", this.state)
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
      window.location.reload(false);
    }
  };

  render() {
    return (
      <div className="Login-Container">
        <h1
          style={{
            color: "white",
            position: "absolute",
            textAlign: "center",
            top: "30px",
            margin: "auto",
            marginRight: "0",
            marginLeft: "0",
            left: "32%",
          }}
        >
          Register
        </h1>
        <input
          className={this.state.showError ? "danger" : ""}
          onChange={this.handlerChange}
          required
          id="Username"
          type="text"
          placeholder="Username"
          name="Username"
        />
        <input
          className={this.state.showEmailError ? "danger" : ""}
          style={{ marginTop: "20px" }}
          onChange={this.handlerChange}
          required
          id="Email"
          type="text"
          placeholder="Email"
          name="Email"
        />

        <input
          className={this.state.showPasswordError ? "danger" : ""}
          style={{ marginTop: "20px" }}
          required
          onChange={this.handlerChange}
          id="Password"
          type="password"
          placeholder="Password"
          name="Password"
        />
        <input
          className={this.state.showPasswordError ? "danger" : ""}
          style={{ marginTop: "20px" }}
          required
          onChange={this.handlerChange}
          id="Confirm"
          type="password"
          placeholder="Confirm Password"
          name="Password"
        />
        <input
          style={{
            marginTop: "20px",
            position: "relative",
            top: "125px",
            left: "-20px",
            height: "35px",
          }}
          onClick={this.handleSubmit}
          type="button"
          value="Register"
        />
      </div>
    );
  }
}
