import axios from 'axios';
import React, { Component } from 'react';
import $ from "jquery";
import Login from './Login';
import Register from './Register';




export default class form extends Component {
  state={
  Username:"",
  Password:"",
  Email:"",
  showLogin:false,
  showError:false,
  showPasswordError:false,
  showEmailError:false
  }
   handlerChange=(e)=>{
    e.preventDefault()
    this.setState({[e.target.name]:e.target.value});
    this.setState({showError:false});
    this.setState({showPasswordError:false});
    this.setState({showEmailError:false});
    console.log()
  }
  
  changeButton=()=>{
    this.setState({showLogin:!this.state.showLogin})
  }   
  
   emailIsValid= (email)=> {
    return /\S+@\S+\.\S+/.test(email)
  }
  
handleSubmit=(e)=>{
  var Username=document.querySelector("#Username1").value;
  var Password=document.querySelector("#Password1").value;
  var Email=document.querySelector("#Email1").value;
  var Confirm=document.querySelector("#Confirm1").value;
  
if(Username === "" || Password === ""|| Email ==="" ){
 return   this.setState({showError:true});
    
    
}
else if(this.emailIsValid(Email)===false){
  return  this.setState({showEmailError:true})
}
 
else if(Password !== Confirm){
  return   this.setState({showPasswordError:true});
}
 
else{
    console.log(this.state)
  axios
  .post("http://localhost:3001/users",this.state)
  .then(response=>{
    console.log(response)
  })
  .catch(error=>{
    console.log(error)
  })
  window.location.reload(false);
}
}
 componentDidMount(){
  
   
  $(document).ready(function(){
  $('.login-info-box').fadeOut();
  $('.login-show').addClass('show-log-panel');
});


$('.login-reg-panel input[type="radio"],.register-info-box input[type="radio]"').on('change', function() {
  if($('#log-login-show').is(':checked')) {
      $('.register-info-box').fadeOut(); 
      $('.login-info-box').fadeIn();
      
      $('.white-panel').addClass('right-log');
      $('.register-show').addClass('show-log-panel');
      $('.login-show').removeClass('show-log-panel');
      
  }
  else if($('#log-reg-show').is(':checked')) {
      $('.register-info-box').fadeIn();
      $('.login-info-box').fadeOut();
      
      $('.white-panel').removeClass('right-log');
      
      $('.login-show').addClass('show-log-panel');
      $('.register-show').removeClass('show-log-panel');
  }
  
});}

shouldComponentUpdate() {
  return true
}
    
   
  render() {
    
    return (
      <div className="form-container">
      <div class="login-reg-panel">
		<div class="login-info-box">
			<h2>Have an account?</h2>
			<p style={{fontSize:"20px"}}>Login with below button.</p>
			<label id="label-register" for="log-reg-show">Login</label>
			<input  type="radio" name="active-log-panel" id="log-reg-show"  checked="checked"/>
		</div>
							
		<div class="register-info-box">
			<h2>Don't have an account?</h2>
			<p style={{fontSize:"20px"}}>Register with below button.</p>
			<label id="label-login" for="log-login-show">Register</label>
			<input className="register-input" type="radio" name="active-log-panel" id="log-login-show"/>
		</div>
							
		<div class="white-panel">
			<div class="login-show">
				<h2 style={{color:"white"}}>LOGIN</h2>
				<input style={{position:"relative",top:"30px"}} type="text" placeholder="Email"/>
				<input style={{position:"relative",top:"30px"}}  type="password" placeholder="Password"/>
				<input style={{backgroundColor:"rgb(23, 136, 42)",color:"white"}} className="bigPagebutton" type="button" value="Login"/>
				<a style={{color:"white"}} href="/">Forgot password?</a>
			</div>
			<div class="register-show">
				<h2 style={{color:"white"}}>REGISTER</h2>
        <input onChange={this.handlerChange} id="Username1"  type="text" placeholder="Username" name="Username"/>
				<input style={{marginTop:"20px"}} onChange={this.handlerChange} id="Email1"  type="text" placeholder="Email" name="Email"/>
        {
          this.state.showEmailError ? <p id="alert" class="alert alert-danger" role="alert">
          Email format is not correct!
        </p>:null
        }
				<input style={{marginTop:"20px"}} onChange={this.handlerChange} id="Password1"  type="password" placeholder="Password" name="Password"/>
        {
          this.state.showPasswordError ? <p id="alert" class="alert alert-danger" role="alert">
          Password and Confirm Password not same!
        </p>:null
        }
				<input style={{marginTop:"20px"}} onChange={this.handlerChange} id="Confirm1" type="password" placeholder="Confirm Password" name="Password"/>
        {
          this.state.showError ? <p id="alert-p" class="alert alert-danger" role="alert">
          İnputs cant be empty!
        </p>:null
        }

       
				<input style={{position:"relative",top:"20px",backgroundColor:"rgb(23, 136, 42)",color:"white"}} onClick={this.handleSubmit}   type="button" value="Register"/>
			</div>
		</div>
	</div>
  <div className="Form-Container">
    
   
    {  this.state.showLogin ? <Login/>:<Register/> }
    
    {
      this.state.showLogin ? <button onClick={()=>this.changeButton()} className="change-button">Go To Register</button>:<button onClick={()=>this.changeButton()} className="change-button"> Go To Login</button>
    }
  </div>
  </div>
    )
  }
}
