import React, { Component } from 'react'

export default class Login extends Component {
    render() {
        return (
            <div style={{position:"relative",top:"80px"}} className="Login-Container">
                <h1 style={{color:"white",position:"relative",textAlign:"center",top:"120px"}}>Login</h1>
                <input style={{marginTop:"25px",positon:"relative",top:"150px",width:"100%",maxWidth:"250px"}} placeholder="Username" type="text" name="Username"/>
                <input style={{marginTop:"25px",positon:"relative",top:"150px",width:"100%",maxWidth:"250px"}} placeholder="Password" type="password" name="Username"/>
                <input style={{left:"-20px"}} placeholder="Login" type="button" value="Login"/>
            </div>
        )
    }
}
